import React,{useState} from 'react'


const AddCommentForm =({articleName,setArticleInfo})=>{


    const [username,setUserName]=useState('');
    const [commentText,setCommentText]=useState('')
    const AddComment =async()=>{
        const result =await fetch(`/api/article/${articleName}/add-comment`,{
            method:'post',
            body:JSON.stringify({userName:username,text:commentText}),
            headers:{
                'Content-Type':'application/json'
            }
        });//end of fetch request

        const body =await result.json();
        setArticleInfo(body);
        setUserName('');
        setCommentText('');
    }

    
    return(
        <div id="add-comment-form">
            <h3>Add a comment</h3>
            <label>
                Name:<input type="text" value={username} onChange ={(e)=>setUserName(e.target.value)}/>
            </label>
            <label>
                comment:<textarea rows='4' cols='50' value={commentText}  onChange ={(e)=>setCommentText(e.target.value)}/>
            </label>
            <button onClick ={()=>AddComment()}>Submit</button>
        </div>
    )

};

export default AddCommentForm;
